.button {
  font-size: 20px;
  color: #fff;
  width: 100%;
  cursor: pointer;
  max-width: 80vw;
  text-align: center;
  padding: 5px 20px;
  border: 0.4rem solid #fff;
  border-radius: 2rem;
  text-shadow: -0.2rem -0.2rem 1rem #fff, 0.2rem 0.2rem 1rem #fff, 0 0 2rem #f40,
    0 0 4rem #f40, 0 0 6rem #f40, 0 0 8rem #f40, 0 0 10rem #f40;
  box-shadow: 0 0 0.5rem #fff, inset 0 0 0.5rem #fff, 0 0 2rem #08f,
    inset 0 0 2rem #08f, 0 0 4rem #08f, inset 0 0 4rem #08f;
}
