.questionContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.questionContainer p {
  color: #fff;
  font-size: 32px;
  font-weight: 700;
  min-height: 30px;
  font-weight: bolder;
  text-align: center;
  text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 15px #e91e63, 0 0 20px #e91e63, 0 0 25px #e91e63, 0 0 30px #e91e63, 0 0 35px #e91e63;
  line-height: 1.3;
}