.button {
  background-image: linear-gradient(160deg, #1086e8, #b800e6);
  border-radius: 100px;
  width: 80px;
  height: 80px;
  cursor: pointer;
}

.icon {
  width: 50px;
}

@media screen and (max-width: 960px) {
  .icon {
    width: 30px;
  }
  
  .button {
    width: 50px;
    height: 50px;
  }
}
