.button {
  font-family: beon;
  font-weight: bolder;
  white-space: normal;
  cursor: pointer;
  border-radius: 15px;
  width: 400px;
  max-width: 85vw;
  height: 75px;
  font-size: 30px;
  text-transform: uppercase;
  margin: 3px auto;
  transition: box-shadow .5s ease;
  text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 15px #08f, 0 0 20px #08f, 0 0 25px #08f, 0 0 30px #08f, 0 0 35px #08f;
}

.label {
  margin-bottom: 3px;
  color: #fff;
  background-size: cover!important;
}

.button:hover {
  border: yellow 3px solid;
}