.instructionsContainer {
  box-shadow: 0 2px 1px -1px rgb(0 0 0 / 20%), 0 1px 1px 0 rgb(0 0 0 / 14%), 0 1px 3px 0 rgb(0 0 0 / 12%);
  border: 5px solid #1086e8;
  width: 700px;
  max-width: calc(100vw - 50px);
  background: rgba(0,0,0,.67);
  padding: 10px;
  height: 60vh;
  overflow-y: auto;
  margin: auto;
}
