body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.main-background {
  background-size: cover !important;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
  background-position: center; 
  background-repeat: no-repeat;
  transition: all 2s;
}

.textCenter {
  text-align: center;
}

* {
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}
@font-face {
  font-family: 'beon';
  src: url('./assets/fonts/beon.eot');
  src: url('./assets/fonts/beon.eot') format('embedded-opentype'),url('./assets/fonts/beon.woff') format('woff'),url('./assets/fonts/beon.ttf')  format('truetype'), url('./assets/fonts/beon.svg') format('svg');
}

@font-face {
  font-family: 'digit';
  src: url('./assets/fonts/ds-digib.ttf') format('truetype');
}

@font-face {
  font-family: 'ardestine';
  src: url('./assets/fonts/ardestine.ttf') format('truetype');
}
